    .total .used {
      background-color: unset;
    }

    .current .needs {
      color: #aaa;
      font-size:.8em;
      padding: 12px 0;
    }

    .current .needs.active {
      cursor: pointer;
      background: yellow;
    }

    .total.active {
      background: unset;
    }

    .input.total {
      background: unset;
      cursor: default;
    }

</style>
